.navigation-drawer {
    max-width: 100%;
    overflow-y: auto;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
    padding: 0 0 100px;
    pointer-events: auto;
    position: fixed;
    transition: .3s cubic-bezier(.25, .8, .5, 1);
    top: 0;
    left: 0;
    will-change: transform;
    z-index: 3;
    /* display: flex; */
}

.navigation-drawer__border {
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    width: 1px
}

.navigation-drawer.navigation-drawer--right:after {
    left: 0;
    right: auto
}

.navigation-drawer:not(.navigation-drawer--is-booted) {
    transition: none!important;
    z-index: -1
}

